/* ======= CARDS ======= */

$cards: ".cl-card, .cl-dcard, .cl-scard";
$adcards: ".cl-card, .cl-dcard";

#{$cards} {

    display: inline-block;
    width: 100%;
    max-width: 100%;
    margin: 0 0 2rem;
    background: $white;
    box-shadow: $level1;
    box-sizing: border-box;
    color: inherit;
    border-radius: 0;
    overflow: hidden;
    position: relative;
    top: 0;
    transition: width .2s ease-in-out, top .1s ease-in-out, box-shadow .1s ease-in-out;

	* {
        box-sizing: border-box;
    }

}

@media screen and (min-width: $break1) {
    #{$cards} {
        &.left {
            float: left;
            margin: 0 2rem 2rem 0;
            max-width: $maxcardwidth;
        }
        &.right {
            float: right;
            margin: 0 0 2rem 2rem;
            max-width: $maxcardwidth;
        }
    }
}

#{$adcards} {

    &:hover,
    &:focus {
        top: -2px;
        box-shadow: $level2;
        cursor: pointer;
        color: inherit;
    }

    &:active {
        box-shadow: $level3;
        color: inherit;
    }

}

.cl-card .cl-card-text,
.cl-tiles .cl-card .cl-card-text,
.cl-scard .cl-scard-text,
.cl-tiles .cl-scard .cl-scard-text {
    padding: 1.5rem 1.2rem 0;
}

.cl-card,
.cl-tiles .cl-card,
.cl-scard,
.cl-tiles .cl-scard {

    p {
        margin-bottom: 2rem;
    }

    img {
        width: 100%;
        height: auto;
        margin: 0;
        padding: 0;
        display: block;
    }

}

.cl-boxout .cl-card img {
	border-radius: 0;
}


/* Action Cards */

.cl-card {

    .cl-button {
        display: block;
        width: 100%;
        max-width: 100%;
        padding: calc(1.5% + 1em) 1rem;
        background: inherit;
        border: none;
        border-top: 1px solid #eee;
        text-align: center;
    }

    &:hover .cl-button,
    &:focus .cl-button,
    .cl-button:hover,
    .cl-button:focus {
        background: $link;
        border-top: 1px solid $link;
        color: $white;
    }

}


/* Detail Cards */

.cl-dcard:after {
    content: '';
    display: block;
    clear: both;
}

.cl-dcard,
.cl-tiles .cl-dcard {

    img {
        float: right;
        width: 30%;
        margin: .5rem .5rem .5rem 1rem;
    }

    .cl-dcard-text {
        padding: 1rem 1rem 0;
    }

    p {
        font-size: .9rem;
        margin-bottom: 1.2rem;
    }

}


/* Story Cards */

.cl-scard {

    box-shadow: none;
    border: 1px solid #eee;

    .cl-scard-img-container {
        position: relative;
        display: block;
    }

    .cl-scard-img-overlay {

        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        border-radius: .2rem .2rem 0 0;
        background: rgba(0,0,0,.5);
        opacity: 0;
        text-align: center;

        &:hover,
        &:focus {
            opacity: 1;
        }

        span {
            display: inline-block;
            position: relative;
            font-size: 1.25rem;
            top: calc( 50% - .5rem );
            left: -.625rem;
            padding-left: 1.85rem;
            color: #fff;
        }

        span:before {
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            content: '';
            width: 1.35rem;
            height: 1.25rem;
            background-image: url(../i/read.png);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 100% auto;
        }

    }

    .cl-scard-category {
        display: inline-block;
        font-size: 1rem;
        margin-bottom: 1rem;
        letter-spacing: 0.05rem;
    }

    .cl-scard-body {

        color: inherit;

        &:hover,
        &:focus,
        &:active {

            h2, p {
                color: $link !important;
            }

        }

    }

    .cl-scard-meta {

        margin-bottom: .9rem;
        font-size: .9rem;
        border-top: 1px solid #000;
        padding-top: 1rem;

        &:after {
            display: block;
            content: '';
            clear: both;
        }

    }

    .cl-scard-date {
        line-height: 20px;
        float: left;
    }

    .cl-scard-social {
        float: right;
    }

}
